.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.App-link {
  color: #61dafb;
}

.content{
  margin: auto;
  display: flex;
  background-color: #282c34;
  justify-content: center;
  align-items: center;
  color: rgb(7, 211, 126);
  text-align: center;
  font-size: 24px; 
  font-family: Arial, Helvetica, sans-serif ;
}
#myRange{
  height: 80px;
}

#play-button{
  height: 50px;
  width: 90px;
  background-color: transparent;
  color:yellowgreen;
}
#play-button:hover{
  cursor: pointer;
  background-color: rgba(240, 248, 255, 0.363);
}
